import { throttle } from 'lodash';
import { useCallback, useEffect } from 'react';
export default (dispatch, throttleTime = 50) => {
    const handleUpdate = (action, value) => {
        dispatch(action(value));
    };
    const throttledHandleChange = useCallback(throttle(handleUpdate, throttleTime), [dispatch]);
    useEffect(() => {
        return () => throttledHandleChange.cancel();
    }, []);
    return throttledHandleChange;
};
