import { Actions } from '../../../../types';
const reducer = (state, action) => {
    const { type, payload } = action;
    switch (type) {
        case Actions.UPDATE_VOLUME:
            return {
                ...state,
                volume: payload,
            };
        case Actions.UPDATE_PAN:
            return {
                ...state,
                pan: payload,
            };
        default:
            return state;
    }
};
export default reducer;
