import { Actions } from '../../../../types';
export const updateWaveform = (waveform) => ({
    type: Actions.UPDATE_WAVEFORM,
    payload: waveform,
});
export const updateDetune = (detune) => ({
    type: Actions.UPDATE_DETUNE,
    payload: detune,
});
export const updateAttack = (attack) => ({
    type: Actions.UPDATE_ATTACK,
    payload: attack,
});
export const updateDecay = (decay) => ({
    type: Actions.UPDATE_DECAY,
    payload: decay,
});
export const updateSustain = (sustain) => ({
    type: Actions.UPDATE_SUSTAIN,
    payload: sustain,
});
export const updateRelease = (release) => ({
    type: Actions.UPDATE_RELEASE,
    payload: release,
});
export const updatePitchbend = (pitchbend) => ({
    type: Actions.UPDATE_PITCHBEND,
    payload: pitchbend,
});
