import { Actions } from '../../../../types';
export const updateLFO1Waveform = (waveform) => ({
    type: Actions.UPDATE_LFO_1_WAVEFORM,
    payload: waveform,
});
export const updateLFO2Waveform = (waveform) => ({
    type: Actions.UPDATE_LFO_2_WAVEFORM,
    payload: waveform,
});
export const updateLFO1Frequency = (frequency) => ({
    type: Actions.UPDATE_LFO_1_FREQUENCY,
    payload: frequency,
});
export const updateLFO2Frequency = (frequency) => ({
    type: Actions.UPDATE_LFO_2_FREQUENCY,
    payload: frequency,
});
export const updateLFO1Amplitude = (amplitude) => ({
    type: Actions.UPDATE_LFO_1_AMPLITUDE,
    payload: amplitude,
});
export const updateLFO2Amplitude = (amplitude) => ({
    type: Actions.UPDATE_LFO_2_AMPLITUDE,
    payload: amplitude,
});
