import { Actions } from '../../../types';
export const updateMIDILoading = (loading) => ({
    type: Actions.UPDATE_MIDI_LOADING,
    payload: loading,
});
export const updateMIDIError = (error) => ({
    type: Actions.UPDATE_MIDI_ERROR,
    payload: error,
});
export const updateMIDIInput = (input) => ({
    type: Actions.UPDATE_MIDI_INPUT,
    payload: input,
});
