import { Actions } from '../../../types';
export const reducer = (state, action) => {
    const { type, payload } = action;
    switch (type) {
        case Actions.UPDATE_LFO_1_TARGET:
            return {
                ...state,
                lfo1Target: payload,
            };
        case Actions.UPDATE_LFO_2_TARGET:
            return {
                ...state,
                lfo2Target: payload,
            };
        default:
            return state;
    }
};
