import React, { useReducer } from 'react';
import initialState from './store/initialState';
import reducer from './store/reducer';
export const lfoStateContext = React.createContext(undefined);
export const LFOStateProvider = ({ children }) => {
    const [state, dispatch] = useReducer(reducer, initialState);
    return (React.createElement(lfoStateContext.Provider, { value: { state, dispatch } }, children));
};
