import { Actions } from '../../../../types';
export const updateFilterType = (filterType) => ({
    type: Actions.UPDATE_FILTER_TYPE,
    payload: filterType,
});
export const updateFilterCutoff = (cutoff) => ({
    type: Actions.UPDATE_FILTER_CUTOFF,
    payload: cutoff,
});
export const updateFilterResonance = (resonance) => ({
    type: Actions.UPDATE_FILTER_RESONANCE,
    payload: resonance,
});
