import { Actions } from '../../../../types';
const reducer = (state, action) => {
    const { type, payload } = action;
    switch (type) {
        case Actions.UPDATE_FILTER_TYPE:
            return {
                ...state,
                type: payload,
            };
        case Actions.UPDATE_FILTER_CUTOFF:
            return {
                ...state,
                cutoff: payload,
            };
        case Actions.UPDATE_FILTER_RESONANCE:
            return {
                ...state,
                resonance: payload,
            };
        default:
            return state;
    }
};
export default reducer;
